<template>
  <div id="upload-first" class="wrap-upload">
    <div class="content-title">
      <div class="first-title"></div>
      <div class="second-title blue-font">UPLOAD WORKS AND SIGN UP</div>
    </div>
    <div class="info">
      <div class="nav-info">
        QQ
      </div>
      <form @submit.prevent="submitForm($event)" action="index_submit" method="get" accept-charset="utf-8">
        <div class="form-line">
          <label for="">QQ</label>
          <input type="text" v-model="formData.captainQQ" placeholder="10QQ">
        </div>
        <div class="form-line">
          <label for=""></label>
          <input type="text" v-model="formData.name">
        </div>
        <div class="form-line">
          <label for=""></label>
          <input type="text" v-model="formData.colleges">
        </div>
        <div class="form-line">
          <label for=""></label>
          <input type="text" v-model="formData.grade">
        </div>
        <div class="form-line">
          <label for=""></label>
          <input type="text" v-model="formData.pro">
        </div>
        <div class="form-line">
          <label for=""></label>
          <input type="text" v-model="formData.phone" placeholder="11">
        </div>
        <div class="form-line">
          <label for=""></label>
          <input type="text" placeholder="" :disabled="!verificationDis" v-model="verification" class="verification">
          <input :value="authCodeInput" type="button" class="get" @click="getVerification">
        </div>
        <div class="form-line">
          <label for=""></label>
          <input type="text" v-model="formData.mail">
        </div>
        <div class="form-line">
          <label for=""></label>
          <input type="text" v-model="formData.teamName">
        </div>
        <div class="form-line">
          <label for=""></label>
          <input type="text" v-model="formData.members" placeholder="QQ:">
        </div>
        <div class="form-line">
          <label for=""></label>
          <textarea name="" id="" cols="30" rows="5" v-model="formData.teamInfo"></textarea>
        </div>
        <!-- form@clickprevent -->
        <div class="form-line submit">
          <label for=""></label>
          <input type="submit" class="next" value="" :disabled="!submitStatus">
          <a target="_blank" href="http://www.pdf995.com/samples/pdf.pdf" class="download"></a>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
export default {
  name: 'upload-first',
  watch: {
    formData:{
      deep:true,
      handler:function(val, oldVal){
        this.submitStatus = true;
      }
    },
    // 
    verification(val, oldVal) {
      const _this = this;
      const _len = 6; // 
      if (val.length == _len) {
        _this.validationVerification(val);
      } else if (val.length > _len) {
        _this.verification = oldVal;
      }
    }
  },
  data() {
    return {
      formData: {
        captainQQ: '',
        name: '',
        colleges: '',
        grade: '',
        pro: '',
        phone: '',
        mail: '',
        teamName: '',
        members: '',
        teamInfo: ''
      },
      verification: '',
      verificationDis: false, // 
      authCodeInput: '',
      Interval: null,
      timer: 60,
      timeStatus: false,
      submitStatus: false

    }
  },
  methods: {

    // 
    submitForm() {
      const _this = this;
      this.$http.post('ossweb-img/mock/submit_info.json', _this.formData).then(function(res) {
        _this.submitStatus = false;
        _this.$emit('accSubmit'); // 
      }).catch(function(err) {
        alert('');
        console.log(err);
      });
    },

    //  
    getVerification() {
      const _this = this;
      _this.$http.get('../../../ossweb-img/mock/verification.json', {

      }).then(function(res) {
        if (res.data.success) {
          _this.submitStatus = true;
          _this.verificationDis = true;
          _this.Interval = setInterval(_this.authGet, 1000);
          $('.verification').val('').focus();
        } else {
          _this.authCodeInput = ''
        }
      }).catch(function(err) {
        _this.authCodeInput = ''
      })
    },

    // 
    validationVerification(val) {
      const _this = this;
      _this.$http.get('../../../ossweb-img/mock/verification.json').then(function(res) {

      }).catch(function(err) {

      })
    },

    // 
    authGet: function() {
      console.log('authGet');
      var _this = this;
      if (_this.timer == 0) {
        _this.timer = 60;
        _this.verificationDis = false;
        _this.authCodeInput = '';
        console.log(_this.timer);
        _this.disabled = false;
        clearInterval(_this.Interval);
      } else {
        _this.timer--;
        _this.authCodeInput = '(' + _this.timer + ')';
      }
    },
  }
}
</script>
<style lang="scss">
.get,
.submit {
  cursor: pointer;
}

@import "./index.scss";
</style>



