<template>
  <div id="qrcode">
    <div class="qrcode">
      <div class="tab">
        <a href="javascript:;" :class="['tab blue-font',qrIndex == 1?'selected':'']" @click="tabClick('1')"></a><a href="javascript:;" class="tab" @click="tabClick('2')" :class="['tab blue-font',qrIndex == 2?'selected':'']"></a>
      </div>
      <div class="content" v-show="qrIndex == 1">
        <div class="qrcode-code">
          <img src="../../../ossweb-img/image/qrcode.jpg" alt="">
        </div>
        <div class="qr-info">
          <p class="blodTitle"></p>
          
        </div>
      </div>
      <div class="content" v-show="qrIndex == 2">
        <div class="qrcode-code">
          <img src="../../../ossweb-img/image/qrcode-2.jpg" alt="">
        </div>
        <div class="qr-info">
          <p class="blodTitle"></p>
          
        </div>
      </div>
    </div>
    <div class="hoverBtn">
      <div class="qr-thum">
        <img src="../../../ossweb-img/image/qr-thum.png" alt="">
      </div>
      <div class="btnInfo"></div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'qrcode',
  data() {
    return {
      qrcodeShow: false,
      qrIndex: 1
    }
  },
  methods: {
    tabClick(index) {
      this.qrIndex = index;
    }
  }
}
</script>
<style lang="scss">
@import "./index.scss";
</style>



